// Promo on City campaigns
.promo__wrapper {
    display: flex;
    flex-direction: column;
    padding-top: $measure--half;
    grid-column: 1/-1;

    .promo__heading {
        text-align: center;
    }

    .promo {
        margin-top: $measure--half;

        .slider, .related-content {
            max-width: inherit;
            padding-left: $measure--small4;
            padding-right: $measure--small4;

            @include media('<tablet') {
                margin-left: 0;
                margin-right: 0;
            }
            
            .card {

                &:focus {
                    @include outline-properties($color-purewhite);
                    outline-offset: -#{$border-width-thick};
                }
                .card__details {
                    display: flex;
                    flex-direction: row;
                    
                    @include media('<tablet') {
                        flex-direction: column;
                    }

                    .card__thumbnail {
                        overflow: hidden;
                        position: relative;
                        padding-bottom: $measure--small4;
                        align-self: stretch;
                        flex-basis: 50%;

                        @include media('<tablet') {
                            padding-bottom: 0;
                        }

                        img {
                            z-index: z-index('above');
                            position: relative;

                            @include media('<tablet') {
                                margin-left: $measure--small3;
                            }
                        }

                        &::after {
                            background-color: $color-purewhite;
                            position: absolute;
                            width: 100%;
                            height: 100%;
                            margin-left: $measure--small4;
                            z-index: z-index('default');
                            content: "";
                            left: 0;

                            @include media('<tablet') {
                                margin-left: 0;
                                margin-top: $measure--small3;
                                top: 0;
                            }
                        }
                    }
                
                    .card__details__container {
                        overflow: hidden;
                        align-self: stretch;
                        flex-basis: 50%;
                        display: flex;
                        padding-top: $measure--small4;
                
                        @include media('<tablet') {
                            padding-top: 0;
                        }
        
                        .card__details__text {
                            height: 100%;
                            padding: $measure--small2;
                            width: 100%;

                            @include media('<tablet') {
                                width: 100%;
                                padding-top: 0;
                            }

                            // for if promo is a testimonial and need quotation marks
                            .card--testimonial__quote {
                                position: relative;
                                padding-left: $measure--threequarters;

                                p {
                                    &:before,
                                    &:after {
                                        @include standfirst();
                                        display: inline;
                                        /**
                                        * Quotation mark line height equal to other lines, even though
                                        * the font size is greater.
                                        **/
                                        line-height: $line-height-ratio--override--alpha;
                                    }

                                    &:before {
                                        content: '';
                                    }
                        
                                    &:after {
                                        content: '';
                                    }
                                }
                            }
                        }
                    }
                }
            }

            + .slider__controls {
                margin-right: $measure--small4;
                max-width: inherit;
            }
        } 
        
        .related-content:not(.slider) {
            list-style-type: none;

            li:only-child {
                grid-column: 1/-1;
            }

            .card__details__text {
                background-color: $color-purewhite;
            }
        }  
    }  
}

// Default colours 
.promo__wrapper {
    background-color: $color-testimonial-bg-default;

    .promo__heading {
        color: $color-testimonial-text-default;
    }

    .promo {
        .slider[data-style="arrows"] {
            .card__details__text {
                background-color: $color-testimonial-text-default;
            }

            + .slider__controls {
                .slider__indicator {
                    .slider__indicator__divider, .slider__indicator__current, .slider__indicator__total {
                        color:  $color-testimonial-text-default;
                    }
                }
    
                 .slider__controls__next, .slider__controls__prev {
                    @include control-button($color-testimonial-text-default);

                    &:focus {
                        background-color: transparent;
                    }
                    &:hover,
                    &:active {
                        color: $color-testimonial-text-default;
                        background-color: $color-testimonial-text-default;
                    }
        
                    &:hover,
                    &:focus:focus,
                    &:active:active {
                        outline-color: $color-testimonial-text-default;
                    }
                 }
            }
        } 
    }
}

// Bayes colours

@each $theme, $colors in $clp-themes {
    $bg-color-lead: nth($colors, 1);
    $txt-color-lead: nth($colors, 2);

    .city-lite--#{$theme} {
        .promo__wrapper {
            background-color: $bg-color-lead;
        }
        .promo__heading {
            color: $txt-color-lead;
        }

        .promo {
            .slider[data-style="arrows"] {
                .card__details__text {
                    background-color: $color-testimonial-text-default;
                }
                + .slider__controls {
                    .slider__indicator {
                        .slider__indicator__divider, .slider__indicator__current, .slider__indicator__total {
                            color: $txt-color-lead;
                        }
                    }

                    .slider__controls__next, .slider__controls__prev {
                        @include control-button($txt-color-lead);

                        &:focus {
                            background-color: transparent;
                        }
                        &:hover,
                        &:active {
                            color: $txt-color-lead;
                            background-color: $bg-color-lead;
                        }

                        &:hover,
                        &:focus:focus,
                        &:active:active {
                            outline-color: $txt-color-lead;
                        }
                    }
                }
            }
        }
    }
}
