@charset "UTF-8";
.sr-only-focusable-within:active, .sr-only-focusable-within:focus, .sr-only-focusable-within:focus-within {
    clip: auto;
    height: auto;
    margin: 0;
    overflow: visible;
    position: static;
    width: auto;
}

.sr-only {
    border: 0;
    clip: rect(0, 0, 0, 0);
    height: 1px;
    margin: -1px;
    overflow: hidden;
    padding: 0;
    position: absolute;
    width: 1px;
}

/**
    Used where card has two divs, one most likely
    being a thumbnail.
**/
/*
    USAGE:

    $pseudo: both / before / after
    $background-color: any hex color
    $skew: a degree value

    Background color must match background color of element mixin is applied to
*/
.heading-centered {
    text-align: center;
}
.heading-centered:after {
    content: "";
    width: 2.5rem;
    height: 0.25rem;
    display: block;
    background: #1e1e1e;
    margin: 0.25rem auto 1rem auto;
}

html {
    text-size-adjust: 100%;
}

body {
    margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
figure,
ol,
ul,
dl,
dt,
dd,
fieldset {
    border: 0;
    margin: 0;
    padding: 0;
}

main {
    display: block;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-size: inherit;
}

b,
strong {
    font-weight: 700;
}

a,
button {
    color: inherit;
}

a {
    text-decoration: none;
}

button {
    background: none;
    border: 0;
    cursor: pointer;
    font: inherit;
    letter-spacing: inherit;
    overflow: visible;
    padding: 0;
