.events-cards .card--event {
    list-style-type: none;
    max-width: unset;

    .card__anchor {
        @include box-shadow-slim(0.05);
        background-color: $color-purewhite;
        position: relative;
        display: flex;
        flex-direction: row;
        padding: $measure--basic;

        &,
        &:focus,
        &:active,
        &:visited {
            border: none;
        }

        &:hover {
            @include media('hover') {
                @include box-shadow-slim();

                border: none;
            }
        }

        .card--event__label--event {
            @include smalltype();

            color: $color-text--caption;
            font-style: italic;
            margin-left: $measure--basic;
            margin-top: $measure--basic;
        }

        .card--event__label--event + .card__thumbnail {
            margin-top: $content-element-margin;
        }

        .card--event__label--shortdate {
            @include betatype();
            @include heading-style();
            line-height: $measure--small1;
            text-align: center;
            align-self: flex-start;
            background-color: $color-brand-primary;
            color: $color-purewhite;
            display: inline-block;
            font-weight: $font-weight-bold;
            left: 0;
            margin-top: 0;
            position: relative;
            padding: $measure--half;
            top: $measure--medium1;
            transition: background-color $transition-duration ease-in;

            @include media('reducemotion') {
                transition: none;
            }

            span {
                font-weight: $font-weight-text;
                display: block;
            }

            // When no Event label, position short date at top of card
            &:first-child {
                top: 0;
            }
        }

        &:hover {
            .card--event__label--shortdate {
                @include media('hover') {
                    background-color: $color-brand-primary--hover;
                }
            }
        }

        .card__details__text {
            padding-bottom: $measure--basic;
            padding-top: $heading-margin;
        }

        .card--event__label--shortdate
            .card--event__label--event
            + .card--event__label--shortdate {
            top: $measure--medium1;
        }

        .card--event__label--event
            + .card__thumbnail
            + .card--event__label--shortdate {
            top: $measure--medium7;
        }

        .card__thumbnail ~ .card__details__text {
            margin-top: 0;
        }

        .card__type {
            @include smalltype();
            margin-top: $content-element-margin;
            color: $color-text--caption;
        }

        &:after {
            content: unset;
            display: none;
        }
    }
}

@each $theme, $colors in $clp-themes {
    $bg-color-pri: nth($colors, 1);
    $bg-color-sec: nth($colors, 3);
    $txt-color-sec: nth($colors, 4);

    .city-lite--#{$theme} {
        .events-cards .card--event {
            .card--event__label--shortdate {
                background-color: $bg-color-sec;
                color: $txt-color-sec;
            }

            &:hover {
                .card--event__label--shortdate {
                    @include media('hover') {
                        background-color: darken( $bg-color-sec, 6%);
                    }
                }
            }

            .card__tag .icon {
                color: $bg-color-sec;
            }
        }
    }
}
